<template>
  <div class="versions">
    <vx-header :showback="1" :title="$t('settings.hardwareVersionInfo')"></vx-header>
    <div class="vx-container">
      <div class="hardware"></div>
      <div class="list">
        <vx-cell :title="$t('settings.firmwareVersion')" :showLink="false" :value="version.deviceVersion"></vx-cell>
        <vx-cell :title="$t('settings.systemVersion')" :showLink="false" :value="version.androidOSVersion"></vx-cell>
        <vx-cell :title="$t('settings.serverVersion')" :showLink="false" :value="version.serviceVersion"></vx-cell>
      </div>
    </div>
  </div>
</template>

<script>
  import htp from "../../api/http";
  import urls from "../../api/apiurl";

  export default {
    data() {
      return {
        version: {}
      };
    },
    created() {
      this.getDeviceVersion();
    },
    methods: {
      getDeviceVersion() {
        htp.get(urls.getDeviceVersion).then(res => {
          if(res.status === 10000000) {
            this.version = res.data;
          }
        });
      }
    }
  };
</script>

<style lang="less" scoped>
  .versions {
    background-color: #383940;
    .hardware {
      height: 345px;
      background: url('../../assets/settings/VX5S.png') no-repeat center 136px;
      background-size: 606px;
    }
    .list {
      margin-top: 25px;
    }
    .vc-cell {
      border-bottom: 1px solid #4f5054; /*no*/
      &-value {
        font-size: 12px; /*no*/
      }
    }
  }
</style>



